
.video-box {
    width: 100%;
    height: 100%;
    overflow: hidden;
    margin: 0px;
    padding: 0px;
    position: relative; }

video{
    height: 100%;
    width: 100%;
    z-index: -100;
    padding: 0px;
}

/* .nav-box */
.logo, nav{
    position: absolute;
    top: 15%;
    width: 100%;
    height: 100px;
}

.logo-title {
    text-align: center;
    z-index: 100;
    color: white;
    font-family: 'Cafe24Moyamoya-Regular-v1.0';
